body {
  margin: 0;
  padding: 0;
  font-family: Product Sans remote;
}

h1 {
  font-family: Product Sans Bold;
  font-weight: 100;
  color: #5072e8;
}

h3 {
  font-family: Product Sans regular;
  font-weight: 70;
  /*color: #5072e8;*/
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: center;
      align-items: center;
  line-height: 0px;
}

li {
  font-family: Roboto;
}

@font-face {
  font-family: "Product Sans remote";
  font-style: normal;
  font-weight: 400;
  src: url(https://fonts.gstatic.com/s/productsans/v5/HYvgU2fE2nRJvZ5JFAumwegdm0LZdjqr5-oayXSOefg.woff2)
    format("woff2");
}

@font-face {
  font-family: "Product Sans Regular";
  font-style: normal;
  font-weight: 400;
  src: url("./assets/product-sans/Product\ Sans\ Regular.ttf");
}

@font-face {
  font-family: "Product Sans Bold";
  font-style: normal;
  font-weight: 400;
  src: url("./assets/product-sans/Product\ Sans\ Bold.ttf");
}



