.SendForm {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 30px;
    right: 0;
    display: -ms-flexbox;
    display: flex;
}

.SendForm input{
    width: 100%;
    outline: none;
    border: 1px solid rgb(232, 239, 253);
    border-radius: 10px;
    padding: 0px 10px 0px 10px;
}


